import React, { useContext, useState } from "react";

import { format } from 'date-fns';

import Xendit from 'xendit-js-node';

import CartContext from "../context/CartContext";
import Boracay from '../images/tours/img1.png';
import Palawan from '../images/tours/img2.png';
import Cebu from '../images/tours/img3.png';
import Shark from '../images/tours/img4.png';
import Choc from '../images/tours/img5.png';
import Albay from '../images/tours/img6.png';
import Scuba from '../images/tours/img7.png';

import './Cart.css';


const Cart = () => {
  const [cart, setCart] = useContext(CartContext);

  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');

  //State hooks for credit card information
  const [cardNumber, setCardNumber] = useState('');
  const [cardName, setCardName] = useState('');
  const [expMonth, setExpMonth] = useState('');
  const [expYear, setExpYear] = useState('');
  const [cvc, setCvc] = useState('');

  // Add new state for payment status and message
  const [paymentStatus, setPaymentStatus] = useState(null);
  const [paymentMessage, setPaymentMessage] = useState('');

  // Client-side script
// const xendit = new Xendit({ publicKey: 'xnd_public_development_LQ4_A9uSAzrGi6e0vDEDYL7its7QNIqycNSWEK4e9ElRcQOv7_9RXZtQ4i1xSlld' });
const xendit = new Xendit();
Xendit.setPublishableKey("xnd_public_development_LQ4_A9uSAzrGi6e0vDEDYL7its7QNIqycNSWEK4e9ElRcQOv7_9RXZtQ4i1xSlld")

 function xenditResponseHandler(err, creditCardToken) {
  if (err) {
    console.error('Xendit Response Handler Error:', err);
    setPaymentStatus('failure');
    setPaymentMessage('Error in processing payment: ' + err.message);
    return;
  }
  console.log('Xendit Token Response:', creditCardToken);

  if (creditCardToken.status === 'VERIFIED') {
    var token = creditCardToken.id;
    // Insert the token into the form so it gets submitted to the server
  } else if (creditCardToken.status === 'IN_REVIEW') {
    window.open(creditCardToken.payer_authentication_url, 'sample-inline-frame');
  } else if (creditCardToken.status === 'FAILED') {
    setPaymentStatus('failure');
    setPaymentMessage('Payment failed: ' + creditCardToken.failure_reason);
  }
}

  // Inside your component or function
  const handlePayment = async () => {
    try {
      const token = await Xendit.card.createToken(
        {
          amount: 0, // or the actual amount
          card_number: cardNumber, // Use state variable
          card_exp_month: expMonth, // Use state variable
          card_exp_year: expYear, // Use state variable
          card_cvn: cvc, // Use state variable
          is_multiple_use: false,
          should_authenticate: true
        }, 
        xenditResponseHandler
      );
      console.log(token)
      submitPayment(token.id); // Function to send the token to your backend
    } catch (error) {
      console.error('Error generating Xendit token:', error);
    }
  };

 const submitPayment = async (tokenId) => {
  try {
    const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/payment`, {
      // ... [existing fetch setup]
    });

    const result = await response.json();
    if (response.ok && result.success) {
      setPaymentStatus('success');
      setPaymentMessage('Payment Successful!');
      handleClearCart();
      resetFormFields();
    } else {
      throw new Error(result.message || 'Payment failed');
    }
  } catch (error) {
    setPaymentStatus('failure');
    setPaymentMessage(error.message || 'Payment failed');
  }
};

  // Function to reset form fields
  const resetFormFields = () => {
    setFirstName('');
    setLastName('');
    setEmail('');
    setPhone('');
    setCardNumber('');
    setCardName('');
    setExpMonth('');
    setExpYear('');
    setCvc('');
  };

  const handleRemoveItem = (index) => {
    const newCart = cart.filter((_, i) => i !== index);
    setCart(newCart);
  };

  const handleQuantityChange = (index, newQuantity) => {
    const newCart = cart.map((item, i) => {
      if (i === index) {
        return { ...item, people: parseInt(newQuantity, 10) };
      }
      return item;
    });
    setCart(newCart);
  };

  const handleClearCart = () => {
    setCart([]);
  };

  const totalPrice = cart.reduce((total, item) => total + (item.price * item.people), 0);

  return (
    <div style={{ marginTop: '150px' }}>
    {/* Display payment status message */}
    
      <h1 style={{ color: 'blue', textAlign: 'center' }}>Complete Your Reservation</h1>
      {paymentStatus && (
        <div className={`payment-message ${paymentStatus}`}>
          {paymentMessage}
        </div>
      )}
      <div style={{ marginTop: '30px' }} className="cart-page-container">
        {cart.length > 0 && (
          <>

            <div className="form-container">

              <form>
                <div className="form-row">
                  <label className="form-label">
                    First Name*
                    <input type="text" value={firstName} onChange={(e) => setFirstName(e.target.value)} />
                  </label>
                  <label className="form-label">
                    Last Name*
                    <input type="text" value={lastName} onChange={(e) => setLastName(e.target.value)} />
                  </label>
                </div>

                {/* Row for Email and Phone fields */}
                <div className="form-row">
                  <label className="form-label">
                    Email*
                    <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} />
                  </label>
                  <label className="form-label">
                    Phone*
                    <input type="tel" value={phone} onChange={(e) => setPhone(e.target.value)} />
                  </label>
                </div>
              </form>

            </div>

          </>
        )}


        <div className="cart-container">
          <h2>Your Bookings - Total: {totalPrice} php</h2>
          <ul className="cart-list">
            {cart.map((item, index) => (
              <li key={index} className="cart-item">
                <img src={item.image} alt={item.tour} />
                <div className="item-details">
                  <h3>{item.tour}</h3>
                  <p><i className="date-icon"></i> {format(new Date(item.date), 'PPP')}</p>
                  <div>
                    <i className="people-icon"></i>
                    <input
                      type="number"
                      value={item.people}
                      onChange={(e) => handleQuantityChange(index, e.target.value)}
                      min="1"
                    />
                  </div>
                  <p><i className="price-icon"></i> {item.price} php per person</p>
                  <p>Total Price: {item.price * item.people} php</p>
                </div>
                <button className="remove-button" onClick={() => handleRemoveItem(index)}>Remove</button>
              </li>

            ))}

          </ul>
          {cart.length > 0 && <button onClick={handleClearCart}>Clear Cart</button>}
          {cart.length === 0 && <p>Your cart is empty</p>}
        </div>
      </div>
      {/* Credit Card Information Form */}
      {cart.length > 0 && (
        <>
          <div className="form-container2">
            <div className="payment-button-container">
              <button onClick={handlePayment} className="payment-button">
                Pay Now
              </button>
            </div>
            <form>
              <h3>Credit Card Information</h3>

              {/* First Row for Card Number, Exp Month, and Exp Year */}
              <div className="form-row">
                <label className="form-label">
                  Card Number*
                  <input type="text" value={cardNumber} onChange={(e) => setCardNumber(e.target.value)} />
                </label>
                <label className="form-label">
                  Exp Month*
                  <input type="text" value={expMonth} onChange={(e) => setExpMonth(e.target.value)} />
                </label>
                <label className="form-label">
                  Exp Year*
                  <input type="text" value={expYear} onChange={(e) => setExpYear(e.target.value)} />
                </label>
              </div>

              {/* Second Row for Full Name and CVC */}
              <div className="form-row">
                <label className="form-label">
                  Full Name on Card*
                  <input type="text" value={cardName} onChange={(e) => setCardName(e.target.value)} />
                </label>
                <label className="form-label">
                  CVC*
                  <input type="text" value={cvc} onChange={(e) => setCvc(e.target.value)} />
                </label>
              </div>
            </form>
          </div>
          
        </>
      )}
    </div>
  );
};

export default Cart;
