import 'bootstrap/dist/css/bootstrap.min.css';

import Foot from '../../images/footer.svg'; 

export default function Footer(props) {
    return (
        <footer className="navbar navbar-expand-lg navbar-light border-gray-700 mt-40 w-full flex justify-start h-28 " 
          style={{ 
              position: 'relative',
              bottom: 0,
              left: 0, 
              zIndex: 1000, 
              backgroundColor: 'rgba(255, 255, 255, 0.5)',
              borderTopStyle: 'solid',
              borderTopWidth: '1px  ',
          }}
        >

          
            <div className="container-fluid justify-center ml-36">
                <img src={Foot} alt="Filipino Adventures" height="100" className="main-logo" />
                <div className="contact-info">
                    <h4><b>Email: contact@filipinoadventurehub.com</b></h4>
                    <h4><b>CALL US: +6396164212316</b></h4>
                </div>
            </div>

            <div className='flex items-end h-full' style={{ 
                width: '100%',
            }}>
                © 2023 Filipio Adventure. All rights reserved.
            </div>

        </footer>
    );
}

